import React, { useEffect, useRef } from 'react'
import MovingCard from '../components/MovingCard'
import Footer from '../components/Footer'
import ScrollContent from '../components/ScrollContent'
import HorizontalScroll from '../components/HorizontalScroll'
import SwipeCarousel from '../components/test'
import Partners from '../components/Partners'
import TextSection from '../components/TextSection'
import Reviews from '../components/Reviews'
import HomeCard from '../components/HomeCard'
import { RiCheckLine } from 'react-icons/ri'
// import { Helmet } from 'react-helmet-async'
import FAQs from '../components/FAQs'
import { motion } from 'framer-motion'
import DynamicBackground from '../components/DynamicBackground'
import DynamicText from '../components/DynamicText'

const data = [
  {
    text: "Devis gratuit 100% gratuit !",
  },
  {
    text: "Installation et paramtrage d'alarmes et de systmes domotiques",
  },
  {
    text: "Remplacement de vitres et vitrines",
  },
  {
    text: "Pose, rglage et rparation de store et volet roulant",
  },
  {
    text: "Intervention en urgence 24/24",
  }
]

const infoVariants = {
  hidden: { y: -30, opacity: 0 },
  show: { y: 0, opacity: 1 }
}

const Elements = (d) => {
  return (
    <motion.li
      variants={infoVariants}
      className="flex flex-row gap-[.5px]"
    >
      <RiCheckLine fill='green' className='my-auto' size={20} />
      <p>{d.text}</p>
    </motion.li>
  )
}

const wrapperVariants = {
  show: {
    transition: { staggerChildren: 0.25 }
  }
}

const Home = () => {
  return (
    <>
      <div className="grid lg:grid-cols-2 h-screen place-content-center lg:px-4 lg:py-20">
        <div className='hidden lg:block lg:m-auto'>
          <h1 className='text-3xl scale-y-125'>Fermeture Sans Souci,</h1>
          <p className='text-xl'>Votre professionnel de la fermeture en Essonnes</p>
          <motion.ul
            variants={wrapperVariants}
            initial='hidden'
            animate='show'
            className='flex flex-col text-slate-200'
          >
            {data.map((d, i) => (
              <Elements key={i} {...d} />
            ))}
          </motion.ul>
        </div>
        <MovingCard>
          <HomeCard />
        </MovingCard>
      </div>
      <DynamicBackground />
      <DynamicText />
      <TextSection
        title="l'Entreprise"
        text="Sans Souci, votre spcialiste pour effectuer la pose de volets roulants en Essonne ! 
          Confiez-nous votre projet et profitez de nos 26 annes d'expriences dans ce domaine 
          pour assurer la scurit de votre habitation. Forts de notre exprience, nous ralisons 
          galement votre installation domotique, que vous soyez sur vry ou dans l'Essonne. 
          Nous vous garantissons plusieurs prestations soignes ralises dans le respect des 
          dlais pour vous apporter entire satisfaction. Nous sommes  votre coute. Contactez-nous !"
      />
      <ScrollContent />
      {/* <SwipeCarousel /> */}
      {/* <HorizontalScroll /> */}
      <TextSection
        title='Nos Partenaires'
          text="Voici un aperu des entreprises avec lesquelles nous collaborons, chacune tant 
            un expert reconnu dans son domaine respectif. Elles s'engagent  offrir le meilleur 
            rapport qualit-prix, soutenu par des garanties inclus pouvant s'tendre sur plus de 10 ans."
      />
      <Partners />
      {/* <i className="text-yellow-500 w-10 fas fa-star" style={{ fontSize: '1rem' }} /> */}
      <Reviews />
      <FAQs />
      <Footer />
    </>
  )
}

export default Home
