import React from 'react';
import { motion } from 'framer-motion';

const DynamicText = () => {
  return (
    <>
      <motion.section
        variants={wrapperVariants}
        initial='hidden'
        animate='show'
        className='text-white'
      >
        {"Sans Souci, votre spcialiste pour effectuer la pose de volets roulants en Essonne ! Confiez-nous votre projet et profitez de nos 26 annes d'expriences dans ce domaine pour assurer la scurit de votre habitation. Forts de notre exprience, nous ralisons galement votre installation domotique, que vous soyez sur vry ou dans l'Essonne. Nous vous garantissons plusieurs prestations soignes ralises dans le respect des dlais pour vous apporter entire satisfaction. Nous sommes  votre coute. Contactez-nous !".split('').map((letter, i) => (
          <motion.span
            key={i}
            variants={imageVariants}
            className='text-white ease-in-out transition-all duration-500'
          >
            {letter}
          </motion.span>
        ))}
      </motion.section>
      
    </>
  )
}

export default DynamicText;

const wrapperVariants = {
  hidden: { opacity: 0 },
  show: {
    opacity: 1,
    transition: { staggerChildren: 0.005 }
  }
}

const imageVariants = {
  hidden: { opacity: 0 },
  show: { opacity: 1 }
}
